.medical-profile {
  padding: 100px 0;
  .container-fluid {
    max-width: 1050px; }
  &__menu {
    position: relative;
    &:before {
      position: absolute;
      right: -30px;
      content: '\EA08';
      top: 4px;
      font-family: 'iconfont';
      font-size: 18px;
      color: $mud;
      display: none;
      @include r(767) {
        display: block; } }
    .tabs__nav {
      overflow-y: hidden; }
    &__caption {
      @include center;
      border-bottom: 1px solid $beige;
      margin-bottom: 10px;
      overflow: hidden;
      @include r(767) {
        flex-wrap: nowrap;
        width: 900px; }
      li {
        font-size: 20px;
        font-weight: 300;
        line-height: 1.5;
        color: $mud;
        padding-bottom: 13px;
        margin-right: 30px;
        border-bottom: 4px solid $white;
        transition: all 0.3s;
        cursor: pointer;
        @include r(870) {
          font-size: 17px; }
        &:last-child {
          margin-right: 0; }
        &.active {
          border-bottom: 4px solid $very-light-brown; }
        &:hover {
          border-bottom: 4px solid $sandy; } } }
    &__content {
      display: none;
      &.active {
        display: block; }
      .tabs {
        margin-top: 80px;
        display: flex;
        width: 100%;
        @include r(767) {
          display: block; }
        &__caption {
          width: 210px;
          margin-right: 30px;
          @include r(767) {
            @include center-column;
            text-align: center;
            width: 100%;
            margin-bottom: 50px; }
          li {
            font-size: 16px;
            font-weight: 300;
            line-height: 1.88;
            color: $mud;
            margin-bottom: 10px;
            border-bottom: 4px solid $white;
            &.active {
              border-bottom: 4px solid $very-light-brown; }
            &:hover {
              border-bottom: 4px solid $sandy; } } }
        &__content {
          display: none;
          flex: 1;
          &.active {
            display: block; }
          .heading {
            align-items: flex-start;
            p {
              text-align: left;
              margin-bottom: 50px;
              margin-top: 10px;
              font-size: 24px;
              @include r(767) {
                font-size: 18px; } } }
          .medical-profile__content__img {
            width: 100%;
            box-shadow: 0 10px 20px 0 rgba(66, 42, 25, 0.15);
            border: 4px solid #f6efe5;
            overflow: hidden;
            height: 350px;
            margin-bottom: 50px;
            display: block;
            @include r(1024) {
              height: 300px; }
            img {
              width: 100%;
              @include center-relative-x;
              @include r(900) {
                width: 140%; }
              @include r(550) {
                width: auto;
                height: 100%; } } }
          p, li {
            font-size: 20px;
            font-weight: 300;
            line-height: 1.5;
            color: $mud;
            margin-bottom: 20px;
            @include r(767) {
              font-size: 16px; } }
          h3 {
            font-family: 'Anglecia Pro Title';
            font-size: 30px;
            font-weight: 500;
            line-height: 1.33;
            color: $mud-brown;
            margin: 50px 0 30px;
            @include r(767) {
                font-size: 24px; } }
          li {
            position: relative;
            &:before {
              content: '';
              position: absolute;
              left: -40px;
              top: 8px;
              width: 10px;
              height: 10px;
              background: $sand;
              border-radius: 50%; } }
          .tags {
            margin: 45px 0;
            ul {
              justify-content: flex-start; }
            li {
              margin-bottom: 0;
              &:before {
                display: none; } } }
          .medical-profile__content__social {
            @include center-y;
            @include r(767) {
              flex-direction: column; }
            &__block {
              @include r(767) {
                padding-top: 20px; } }
            span {
              font-family: 'SF Pro Display';
              font-size: 16px;
              line-height: 1.25;
              color: $dark-khaki;
              display: block;
              margin-right: 20px; }
            a {
              margin-right: 20px;
              i {
                color: $dark-sky-blue;
                font-size: 20px; } } } } } } } }



