.video {
  padding: 70px 0;
  &__block {
    overflow: hidden;
    position: relative;
    border: 4px solid $white;
    height: 250px;
    overflow: hidden;
    box-shadow: 0 15px 30px 0 rgba(109, 80, 9, 0.4);
    transition: all 0.3s;
    &:hover {
      border: 4px solid #e0bf57;
      box-shadow: 0 20px 30px 0 rgba(109, 80, 9, 0.4);
      a {
        background: none;
        .video__block__text {
          p, span {
            color: transparent; } } }
      i {
        color: $white;
        left: 50%;
        position: absolute;
        transform: translate(-50%); } }
    &:active {
      i {
        color: #e0bf57; } }
    img {
      width: 100%;
      @include r(1024) {
        width: auto;
        height: 100%;
        @include center-relative-x; } }
    a {
      position: absolute;
      top: 0;
      width: 100%;
      height: 100%;
      background: rgba(244, 224, 122, 0.9);
      left: 0;
      @include center;
      transition: all 0.4s;
      i {
        font-size: 78px;
        color: $mud-brown;
        transition: ease-in-out 0.3s;
        position: absolute;
        left: 18%;
        transform: translate(0);
        @include r(1024) {
          left: 15%; }
        @include r(850) {
          left: 80px; }
        @include r(767) {
          font-size: 50px;
          margin-left: 10px; }
        @include r(600) {
          left: 30px; } } }
    &__text {
      margin-left: 100px;
      display: block;
      opacity: 1;
      transition: all 0.3s;
      span, p {
        font-family: 'SF Pro Display';
        font-size: 16px;
        line-height: 1.25;
        color: $mud-brown;
        @include r(767) {
          font-size: 14px; } }
      p {
        font-size: 24px;
        font-weight: 500;
        @include r(767) {
          font-size: 18px; } } } } }
