.license {
  padding: 100px 0;
  &__content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-top: 70px; }
  &__block {
    width: calc(195px - 40px);
    height: 220px;
    position: relative;
    overflow: hidden;
    margin: 0 20px;
    margin-bottom: 55px;
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: url('../img/honors/frame.png');
      background-repeat: no-repeat;
      background-size: 100% 100%; }
    img {
      height: 100%; } }
  .btn {
    @include center-relative-x; } }
