.natural-factors {
  padding: 70px 0;
  .heading {
    margin-bottom: 110px;
    @include r(767) {
      margin-bottom: 50px; } }
  &__block {
    @include center-column;
    &__img {
      width: 165px;
      height: 165px;
      border-radius: 50%;
      background: #f6efe5;
      @include r(767) {
        width: 125px;
        height: 125px; }
      img {
        width: 100%; } }
    p {
      font-size: 24px;
      font-weight: 300;
      color: $mud;
      margin-top: 30px;
      text-align: center;
      @include r(767) {
        margin-bottom: 60px;
        font-size: 18px; } } } }
