.water, .clay {
  background: $very-light-pink;
  overflow: hidden;
  position: relative;
  padding: 50px 0;
  .heading {
    margin-bottom: 60px;
    align-items: flex-start;
    p {
      text-align: left; } }
  &__block {
    @include center-y;
    margin-bottom: 30px;
    img {
      height: 40px; }
    p {
      font-family: 'SF Pro Display';
      font-size: 16px;
      line-height: 1.25;
      color: $dark-khaki;
      margin-left: 10px;
      @include r(767) {
        font-size: 14px; } } }
  .btn {
    margin-top: 20px;
    margin-bottom: 50px; }
  ul {
    @include center-y;
    justify-content: space-between;
    @include r(767) {
      flex-direction: column; }
    a {
      font-family: 'SF Pro Display';
      font-size: 16px;
      line-height: 1.25;
      color: $dark-sky-blue;
      @include r(767) {
        font-size: 14px; } }
    li {
      position: relative;
      display: block;
      margin-right: 10px;
      @include r(767) {
        margin-right: 0; }
      &:after {
        content: '';
        width: 7.9px;
        height: 7.9px;
        background-color: $beige;
        position: relative;
        display: inline-block;
        transform: rotate(45deg);
        margin-left: 40px;
        @include r(1024) {
          display: none; } }
      &:last-child {
        &:after {
          display: none; } } } }
  &__img {
    img {
      width: 490px; }
    @include r(767) {
      display: none; } }
  &__video {
    @include center-column;
    position: relative;
    top: -80px;
    right: -70px;
    @include r(767) {
      display: none; }
    img {
      border-radius: 5px; }
    span {
      font-family: 'SF Pro Display';
      font-size: 16px;
      line-height: 1.25;
      color: $dark-sky-blue; } } }
.water {
  padding-top: 160px;
  @include r(767) {
    padding-bottom: 40px; }
  &:before {
    content: '';
    height: 100px;
    width: 100%;
    background: url('../img/wave-top.svg') no-repeat;
    position: absolute;
    top: 0;
    background-position: 74% 100%;
    background-size: cover;
    transform: rotate(180deg); } }
