.reviews {
  padding: 100px 0;
  .swiper {
    position: relative;
    margin-top: 70px;
    @include r(1024) {
      padding: 0 50px; }
    @include r(767) {
      padding: 0; }
    &-slide {
      @include center;
      @include r(767) {
        flex-direction: column; }
      .slide__img {
        width: 240px;
        height: 240px;
        border-radius: 50%;
        overflow: hidden;
        margin-right: 40px;
        border: 2px solid $very-light-pink;
        @include r(767) {
          width: 100px;
          height: 100px;
          margin-bottom: 20px;
          margin-right: 0; }
        img {
          width: 100%; } }
      .slide__text {
        flex: 1;
        h4 {
          font-family: 'Anglecia Pro Title';
          font-size: 24px;
          font-weight: 500;
          line-height: 1.25;
          color: $mud-brown;
          @include r(767) {
            font-size: 18px;
            text-align: center; } }
        span {
          font-family: 'SF Pro Display';
          font-size: 16px;
          line-height: 1.25;
          color: $dark-khaki;
          margin: 5px 0;
          margin-bottom: 25px;
          display: block;
          @include r(767) {
            font-size: 14px;
            text-align: center;
            margin-bottom: 25px; } }
        p {
          font-family: 'Anglecia Pro Text';
          font-size: 20px;
          font-weight: 300;
          line-height: 1.5;
          color: $mud;
          max-width: 630px;
          @include r(767) {
            font-size: 16px;
            text-align: center; } } } }
    .swiper-button-next, .swiper-button-prev {
      @include r(1024) {
        display: block; }
      @include r(767) {
        top: 50px; }
      i {
        color: $sandy; } }
    .swiper-button-prev {
      left: 15px; }
    .swiper-button-next {
      right: 30px; } } }
