.reservation {
  padding: 140px 0;
  .form {
    @include center-column;
    &__content {
      @include center-y;
      margin: 50px 0;
      @include r(800) {
        flex-direction: column; } }
    &__block {
      @include r(800) {
        margin-bottom: 40px; }
      &-tel {
        margin-bottom: -20px; }
      input {
        font-size: 20px;
        font-weight: 300;
        line-height: 1.25;
        text-align: center;
        color: $sand;
        width: 360px;
        margin-right: 35px;
        background: none;
        border-bottom: 2px solid $sandy;
        padding-bottom: 5px;
        font-family: 'Anglecia Pro Text';
        @include r(767) {
          margin-right: 0;
          width: 300px;
          font-size: 18px; }
        &::placeholder {
          color: $sand; }
        &.phone {
          color: $mud-brown;
          &::placeholder {
            color: $mud-brown; } } }
      span {
        text-align: center;
        display: block;
        font-family: 'SF Pro Display';
        font-size: 12px;
        margin-top: 5px;
        color: $sand; } } } }
